.navbar {
    height: 50px;
    width: 100%;
    padding: 1rem; 
    border-bottom: 1px solid #b3b3b3;
    box-shadow: 0 4px 13px -3px rgba(0, 0, 0, 0.10196);
    background-color: rgb(248, 248, 248);
    font-family: "Google Sans", sans-serif;
    overflow: hidden;
    position: fixed;
    top: 0;
}

.nav-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 50px;
    width: 100%;
  }

  .navbar-brand {
    height: 80%;
    display: inline-block;
    margin: 5px 0.5rem;
  } 

  .navbar-collapse {
    margin: 10px;
     width: 50%;
     display: inline-block;
  } 

  .navbar-collapse ul {
    display: grid;
    grid-template-columns: 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr;
  } 
  
  .navbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .nav-item {
    list-style: none;
    margin: 0 10px;
    font-size: 20px;
  }
  
  .nav-item:hover{
    opacity: 0.7;
  }
  
  .navbar-toggler {
    height: 40px;
    width: 50px;
    border-radius: 10px;
    float: right;
    margin: 5px;
    cursor: pointer;
    display: none;
    border: none;
  }
  
  .navbar-toggler-icon {
    display: inline-block;
    height: 30px;
    width: 30px;
    vertical-align: middle;
    background-image: url("black.png");
    background-repeat: no-repeat;
    background-size: contain;
    opacity: 0.7;
  }

  .nav-container a{
    text-decoration: none;
  }
  
  @media screen and (max-width: 970px) {

    .navbar-toggler{
        display: block;
    }

    .navbar-collapse ul {
        margin: 5px 10px;
        display: grid;
        grid-template-columns: repeat(4, 2.5fr);
    }

    .nav-item{
        height: 40px;
    }

    /* .navbar-collapse {
        display: none;
    } */

    /* .navbar{
        height: 130px;
    } */

}
  
  @media screen and (max-width: 500px) {

    .navbar-collapse ul {
        display: grid;
        grid-template-columns: repeat(2, 5fr);
    }

    /* .navbar{
        height: 210px;
    } */

}