.row{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  
  .col{
    margin: 20px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media screen and (max-width: 1100px) {

    .row{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

  }
  
  @media screen and (max-width: 890px) {
  
    .row{
      display: grid;
      grid-template-columns: repeat(1, 1fr);
    }
  
  }