.news-container{
    width: 100vw;
  }
  
  .news-container h1{
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Fjalla One', sans-serif;
    font-weight: 800;
    margin: 80px 30px 30px 30px;
  }
  
  
  
    .card {
      width: 20rem;
      border: 1px solid rgb(141, 134, 134);
    }
  
    .Source {
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 1px solid #000;
      background-color: #212123;
    }
  
    .Source strong{
        letter-spacing: 1px;
        color: white;
    }
  
  .card img{
    height: 179px;
    width: 100%;
  }
  
  .card-body{
    padding: 10px;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
  }
  
  .link{
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.1s;
  }
  
  .link:hover {
    transform: scale(1.1);
  }
  
  .link a{
    margin-top: 20px;
    width: 150px;
    height: 40px;
    background-color: #4e6af6;
    border-radius: 20px;
    text-decoration: none;
    color: antiquewhite;
    text-align: center;
    padding-top: 8px;
  }

  .card-author {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  @media screen and (max-width: 1100px) {
  
    .card {
      width: 25rem;
    }
  
  }
  
